"use strict";

var btnRock = document.getElementById('btn-rock');
var btnPaper = document.getElementById('btn-paper');
var btnScissors = document.getElementById('btn-scissors');
var btnReset = document.getElementById('btn-reset');
var root = document.getElementById('root');
var resultDiv = document.getElementById('result');
var cntWinFinishGame = 3;
var counter = 0;
var winCount = 0;
var looseCount = 0;
var gameResult;
var finalResult;

var btnPlayHandler = function btnPlayHandler(event) {
  if (gameResult && gameResult.parentNode === root) {
    root.removeChild(gameResult);
  }

  if (counter === cntWinFinishGame) {
    resultDiv.removeChild(finalResult);
    counter = 0;
    winCount = 0;
    looseCount = 0;
  }

  counter++;
  gameResult = document.createElement('div');
  gameResult.setAttribute('id', 'game-result');
  root.appendChild(gameResult);
  var randomShape = showRandomShape();
  var btnChosen = event.target.id.slice(4);
  var result = compareShapes(randomShape, btnChosen);

  if (result === 'You’ve LOST!') {
    looseCount++;
  }

  if (result === 'You’ve WON!') {
    winCount++;
  }

  showShape(btnChosen);
  var gameInfo = document.createElement('p');
  gameResult.appendChild(gameInfo);
  var gameInfoText = document.createTextNode("Round ".concat(counter, ", \n      ").concat(btnChosen, " vs ").concat(randomShape, ", \n      ").concat(result));
  gameInfo.appendChild(gameInfoText);
  showShape(randomShape);

  if (counter > 2) {
    return showFinalResult(winCount, looseCount);
  }
};

var btnResetHandler = function btnResetHandler() {
  counter = 0;
  winCount = 0;
  looseCount = 0;

  if (finalResult && finalResult.parentNode === resultDiv) {
    resultDiv.removeChild(finalResult);
  }

  if (gameResult && gameResult.parentNode === root) {
    root.removeChild(gameResult);
  }
};

btnRock.addEventListener('click', btnPlayHandler);
btnPaper.addEventListener('click', btnPlayHandler);
btnScissors.addEventListener('click', btnPlayHandler);
btnReset.addEventListener('click', btnResetHandler);